* {
  box-sizing: border-box; }

body {
  background-color: teal;
  font-family: "SortsMillGoudy";
  font-size: 16px;
  min-height: 100vh;
  padding-top: 35px; }

h1 {
  font-size: 3rem;
  font-family: "GroteskBold"; }
  @media (min-width: 768px) {
    h1 {
      font-size: 4.5rem; } }
  @media (min-width: 1024px) {
    h1 {
      font-size: 5.125rem; } }

h2 {
  font-size: 1.35rem; }
  @media (min-width: 768px) {
    h2 {
      font-size: 2.2rem; } }
  @media (min-width: 1024px) {
    h2 {
      font-size: 2.625rem; } }

h3 {
  font-size: 0.75rem;
  font-family: "GroteskMedium";
  margin-bottom: 0;
  padding-bottom: 10px;
  position: relative; }
  h3:before {
    content: '';
    display: block;
    position: absolute;
    background-color: #c2c2c2;
    bottom: 0;
    height: 1px;
    left: 0;
    width: 16px; }

a {
  color: #000;
  display: inline-block;
  opacity: 1;
  position: relative;
  text-decoration: none;
  transition: opacity 0.25s linear; }
  a:before {
    content: '';
    display: block;
    position: absolute;
    background-color: #000;
    bottom: 0;
    height: 1px;
    opacity: 1;
    width: 100%; }
  a:hover {
    opacity: 0.65; }
    a:hover:before {
      opacity: 0.65; }

@media (min-width: 768px) {
  body {
    border: 10px solid #000;
    padding-top: 50px; } }

@media (min-width: 1024px) {
  body {
    border-width: 15px;
    padding-top: 70px; } }

.container {
  lost-center: 90vw; }

@media (min-width: 768px) {
  .container {
    lost-center: 88vw; } }

@media (min-width: 1024px) {
  @lost gutter 60px;
  .container {
    lost-center: 1300px; } }

.global-nav {
  display: flex;
  width: 100%; }
  .global-nav a:before {
    display: none; }
  .global-nav__logo {
    align-self: flex-start;
    font-family: "GroteskBold";
    font-size: 0.9rem;
    text-transform: uppercase; }
  .global-nav__contact {
    font-family: "GroteskMedium";
    font-size: 0.85rem;
    margin-left: auto; }

.footer {
  margin-top: 110px;
  padding-bottom: 10px; }
  .footer .container {
    border-top: 1px solid #c2c2c2; }
  .footer__section {
    margin-top: 30px; }
    .footer__section:first-child {
      margin-top: 20px; }
  .footer__address p {
    line-height: 1.5;
    margin: 0; }
  .footer__emails a:before {
    display: none; }

@media (min-width: 768px) {
  .footer .container {
    padding-top: 10px; }
  .footer__section {
    margin-top: 0;
    lost-column: 1/3; }
    .footer__section:first-child {
      margin-top: 0; }
    .footer__section:last-child {
      margin-top: 20px;
      lost-column: 3/3; }
  .footer__emails p:first-child {
    margin-top: 3px; } }

@media (min-width: 1024px) {
  .footer {
    margin-top: 205px;
    padding-bottom: 25px; }
    .footer .container {
      padding-top: 20px; }
    .footer__section {
      lost-column: 2/8; }
      .footer__section:last-child {
        margin-top: 0;
        lost-column: 3/8;
        lost-offset: 1/8; }
        .footer__section:last-child p {
          margin-top: 0; } }

.home header {
  padding-top: 110px; }

.home h1 {
  margin-bottom: 0; }

.home h2 {
  margin-top: 10px; }

.home .information {
  margin-top: 60px; }
  .home .information__section {
    margin-top: 45px; }
    .home .information__section:first-child {
      margin-top: 0; }
  .home .information__section-2 p, .home .information__section-3 p {
    max-width: 400px; }
  .home .information__section-content {
    margin-top: 25px; }
    .home .information__section-content p:first-child {
      margin-top: 0; }

@media (min-width: 768px) {
