    margin-top: 35px; }
    .home .information__section {
      margin-top: 0;
      lost-column: 5/10 2 flex; }
      .home .information__section-2, .home .information__section:last-child {
        lost-column: 4/10 flex; }
      .home .information__section:last-child {
        margin-top: 50px; }
      .home .information__section-content {
        margin-top: 40px; } }

@media (min-width: 1300px) {
